import { __awaiter } from "tslib";
import downloadjs from 'downloadjs';
import Group from './components/group.vue';
import Variable from './components/variable.vue';
import Dependencie from './components/dependencie.vue';
import { alas, status, action } from '@/alas';
import { defineComponent, reactive, ref } from '@vue/composition-api';
export default defineComponent({
    props: {},
    components: {
        'self-group': Group,
        'self-variable': Variable,
        'self-dependencie': Dependencie
    },
    setup() {
        // =================
        //
        // state
        //
        let $ = reactive({
            alas,
            project: status.fetch('project'),
            createName: '',
            createGroup: '',
            createCopyTarget: null,
            variable: false,
            group: false,
            removeMode: false,
            dependencie: false
        });
        // =================
        //
        // refs
        //
        let invoke = ref(null);
        let selectSpecInvoke = ref(null);
        let createForm = ref(null);
        // =================
        //
        // methods
        //
        let outputProject = () => __awaiter(this, void 0, void 0, function* () {
            yield $.project.$o.save.start();
            downloadjs(JSON.stringify($.project.$v.output, null, 4), `project-${$.project.name}.json`, 'text/plain');
        });
        let outputJS = () => __awaiter(this, void 0, void 0, function* () {
            yield $.project.$o.save.start();
            let zip = yield $.project.$m.zip();
            downloadjs(zip, `project-${$.project.name}.zip`, 'text/plain');
        });
        let invokeTest = () => {
            selectSpecInvoke.value.open((specs) => __awaiter(this, void 0, void 0, function* () {
                yield $.project.$o.save.start();
                invoke.value.play(specs.map(s => s.id));
            }));
        };
        let target = (spec) => {
            return {
                name: 'project.specs',
                params: {
                    id: spec.id
                }
            };
        };
        let create = () => {
            createForm.value.open(() => {
                if ($.createCopyTarget) {
                    let target = $.createCopyTarget.$export();
                    target.id = undefined;
                    target.name = $.createName;
                    target.group = $.createGroup;
                    $.project.specs.write(target);
                }
                else {
                    $.project.specs.write({
                        name: $.createName,
                        group: $.createGroup
                    });
                }
            });
        };
        let remove = (id) => {
            action.confirm('確定刪除測試？', done => {
                $.project.specs.remove(id);
                done();
            });
        };
        // =================
        //
        // done
        //
        return {
            $,
            invoke,
            selectSpecInvoke,
            createForm,
            outputProject,
            outputJS,
            invokeTest,
            target,
            create,
            remove
        };
    }
});
