import { defineComponent } from '@vue/composition-api';
export default defineComponent({
    props: {
        project: Object
    },
    setup(props) {
        // =================
        //
        // methods
        //
        let remove = (id) => {
            props.project.groups.remove(id);
        };
        let create = () => {
            props.project.groups.write({
                name: '',
                level: 0
            });
        };
        // =================
        //
        // done
        //
        return {
            remove,
            create
        };
    }
});
